@import "ScssHelpers/helpers-all.scss";
@import "recipes-card-mixin";
@import "universal/pagination";

.recipes_list__wrap {
	margin-bottom: 6.6rem;
}

.recipes_list__array {
	@include flex_block(row, wrap, flex-start, stretch, flex-start);

	.our_farmers & {
		@include bp($point_2, min) {
			margin: 0 -2.5rem -9.3rem 0;
		}
	}
	
	@include bp($point_2, min) {
		margin: 0 -2.5rem -5.9rem 0;
	}

	@include bp($point_3) {
		margin: 0 -1.6rem -3rem 0;
	}
}

.recipes_list__item {
	@include bp($point_2, min) {
		width: calc(100% / 3);
		padding: 0 2.5rem 5.6rem 0;
	}

	@include bp($point_3) {
		width: 50%;
		padding: 0 1.6rem 3rem 0;
	}
}
