@import "ScssHelpers/helpers-all.scss";

.recipes_card {
	@include flex_block(column, wrap, flex-start, flex-start);
	height: 100%;

	&:focus,
	&:hover {
		text-decoration: none;
	}

	.featured_recipe__slider & {	
		&:not(:first-child) {
			@include bp($point_2, min) {
				display: none;
			}
		}
	}
}

.recipes_card__picture {
	display: block;
	position: relative;
	width: 100%;
	overflow: hidden;

	.featured_recipe & {
		@include bp($point_4, min) {
			padding-top: 44.4%;
		}

		@include bp($point_4 - 1) {
			padding-top: 53.5%;
			margin-bottom: 1.6rem;
		}
	}
	.featured_recipies & {
		padding-top: 52%;
	}

	@include bp($point_2, min) {
		margin-bottom: 3rem;
		padding-top: 71.4%;
	}

	@include bp($point_3) {
		margin-bottom: 1.4rem;
		padding-top: 82.6%;
	}
}

.recipes_card__img {
	position: absolute;
	top: 0;
	left: 0;
	display: block;
	width: 100%;
	height: 100%;
	object-fit: cover;

	transition: transform .3s ease;

	.recipes_card:hover & {
		transform: scale(1.1);
	}
}

.recipes_card__content_head {
	&:not(.featured_recipe .recipes_card__content_head) {
		flex: auto;
	}

	&:not(:last-child) {
		@include bp($point_2, min) {
			margin-bottom: 1.4rem;
		}

		@include bp($point_3) {
			margin-bottom: 1rem;
		}
	}

	.featured_recipe & {
		&:not(:last-child) {
			@include bp($point_2, min) {
				margin-bottom: 1.6rem;
			}
			@include bp($point_3) {
				margin-bottom: .9rem;
			}
		}
	}
}

.recipes_card__subtitle {
	@include h5;
	text-transform: uppercase;

	&:not(:last-child) {
		@include bp($point_2, min) {
			margin-bottom: 1rem;
		}

		@include bp($point_3) {
			margin-bottom: .4rem;
		}
	}

	.featured_recipe & {
		&:not(:last-child) {
			@include bp($point_2, min) {
				margin-bottom: 0.8rem;
			}
		}
	}
}

.recipes_card__title {
	@include h4;
	@include transition_color;

	color: $black;

	.recipes_card:hover &,
	.recipes_card:focus & {
		color: $gold_2;
	}

	.featured_recipe & {
		@include bp($point_2, min) {
			letter-spacing: -0.016em;
		}

		@include bp($point_3) {
			line-height: 1.14;
    	letter-spacing: -0.025em;
		}
	}

	@include bp($point_2, min) {
		letter-spacing: -0.01em;
		line-height: 1.14;
	}

	//@include bp($point_3) {
	//	font-size: 1.6rem;
	//	letter-spacing: -0.03em;
	//	line-height: 1.25;
	//}
}

.recipes_card__description {
	@include text;

	line-height: 1.5;

	color: $black;

	&:not(:last-child) {
		margin-bottom: 1.8rem;
	}

	@include bp($point_4 - 1) {
		display: none;
	}
}

.recipes_card__list {
	@include flex_block(row, wrap, flex-start, center);

	color: $gold_2;

	@include bp($point_2, min) {
		margin: 0 -3.2rem -2rem 0;
	}

	@include bp($point_3) {
		margin: 0 -1.6rem -1.6rem 0;
	}

	.featured_recipe & {
		@include bp($point_3) {
			margin: 0 -2.5rem -1.6rem 0;
		}
	}
}

.recipes_card__list_item {
	@include flex_block(row, nowrap, flex-start, center);

	@include bp($point_2, min) {
		padding: 0 3.2rem 2rem 0;
	}

	@include bp($point_3) {
		padding: 0 1.6rem 1.6rem 0;
	}

	.featured_recipe & {
		@include bp($point_3) {
			padding: 0 2.5rem 1.6rem 0;
		}
	}
}

.recipes_card__list_icon {
	margin-right: 0.6rem;

	@include bp($point_2, min) {
		width: 2.4rem;
		height: 2.4rem;
	}

	@include bp($point_3) {
		width: 1.6rem;
		height: 1.6rem;
	}

	.featured_recipe & {
		width: 1.9rem;
    height: 2.3rem;

    margin-right: 0.8rem;
	}
}

.recipes_card__list_text {
	font-weight: 400;
	line-height: 1.71;
	text-transform: uppercase;

	@include bp($point_2, min) {
		font-size: 1.4rem;
		letter-spacing: 0.11em;
	}

	@include bp($point_3) {
		font-size: 1rem;
	}

	.featured_recipe & {
		@include bp($point_3) {
			font-size: 1.3rem;
			letter-spacing: .06em;
		}
	}
}
